
.sim-switch {
  display: inline-block;
}
.sim-switch label {
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.sim-switch label > * {
      margin: 0;
}
.sim-switch label > * + * {
        margin-left: 1ch;
}
.sim-switch input {
    -webkit-appearance: none;
       -moz-appearance: none;
            appearance: none;
    position: relative;
    padding: .8ch 1.8ch;
    font: inherit;
    cursor: pointer;
    outline: 0;
    will-change: transform, background;
    -webkit-transition: all var(--ms, var(--default-ms)) ease-out;
    transition: all var(--ms, var(--default-ms)) ease-out;
    background: var(--switch-color, var(--default-switch-color));
}
.sim-switch input:active {
      -webkit-transform: scale(0.9);
              transform: scale(0.9);
}
.sim-switch input::before {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      padding: .9ch;
      -webkit-box-shadow: 0 0 0 1px;
              box-shadow: 0 0 0 1px;
      -webkit-transform: translate(-100%, -50%);
              transform: translate(-100%, -50%);
      background: var(--switch-handle-color, var(--default-switch-handle-color));
      -webkit-transition: -webkit-transform var(--ms, var(--default-ms)) ease-out;
      transition: -webkit-transform var(--ms, var(--default-ms)) ease-out;
      transition: transform var(--ms, var(--default-ms)) ease-out;
      transition: transform var(--ms, var(--default-ms)) ease-out, -webkit-transform var(--ms, var(--default-ms)) ease-out;
}
.sim-switch input:checked {
      background: var(--switch-color-active, var(--default-switch-color-active));
}
.sim-switch input:checked::before {
        -webkit-transform: translate(0%, -50%);
                transform: translate(0%, -50%);
}



