
.sim-badge {
  display: inline-block;
  position: relative;
}
.sim-badge__content {
    position: relative;
    -webkit-transform: translate(-1ex, -1em);
            transform: translate(-1ex, -1em);
    font-size: 0.85em;
    height: 1.5em;
    min-width: 1.5em;
    margin: -.5em 0;
    padding: 0 .5em;
    border-radius: 1em;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    font-family: monospace;
    will-change: font-size;
}
.sim-badge-enter-active, .sim-badge-leave-active {
    -webkit-transition: all var(--ms, var(--default-ms)) cubic-bezier(0.6, -0.4, 0.6, 1.4);
    transition: all var(--ms, var(--default-ms)) cubic-bezier(0.6, -0.4, 0.6, 1.4);
}
.sim-badge-enter, .sim-badge-leave-to {
    font-size: 0em;
}



