
.app-menu ul {
  margin: 0;
  padding: 0;
}
.app-menu ul li {
    list-style: none;
    padding: .5em 0;
}
.app-menu a {
  color: var(--action);
}
.app-menu a.router-link-exact-active {
    color: var(--shade);
}
.app-menu header {
  padding: .5em 1em;
  background: var(--cloud);
  margin: 0 -1em;
}
.app-menu section li {
  padding: .5em 0 .5em 1.8em;
}



