
.demobox {
  position: relative;
}
.demobox__content {
    border: 1px solid var(--cloud);
    background: var(--light);
    border-radius: var(--base-radius);
}
.demobox--output + .demobox--output {
    -webkit-box-shadow: 0 1px 0 0 var(--cloud) inset;
            box-shadow: 0 1px 0 0 var(--cloud) inset;
}
.demobox + .demobox {
    margin-top: 2em;
}
.demobox__intro:not(:empty) {
    padding: 0 0 .5em 0;
}
.demobox__view {
    padding: 1em;
    line-height: 1.3;
}
.demobox__js {
    background: var(--snow);
}
.demobox__html {
    background: var(--snow);
}
.demobox__handle {
    display: inline-block;
    cursor: pointer;
    padding: 1em;
    font-weight: 600;
    color: var(--helper);
    font-size: .8em;
}
.demobox--drawer {
    max-height: 0;
    padding: 0 1em;
    overflow: auto;
    -webkit-transition: var(--ms) ease-in-out;
    transition: var(--ms) ease-in-out;
}
.demobox.open .demobox--drawer:not(:empty) {
    max-height: 90vh;
    padding: 1em;
}
.demobox pre {
    margin: 0;
    white-space: pre-wrap;
}
.demobox pre .de-emphasize {
      color: var(--shade);
}
.demobox pre .emphasize {
      color: hotpink;
      font-weight: bold;
}



