
:root {
  --default-ms: 350ms;
  --default-icon-font-family: 'FontAwesome';
  --icon-check-circle: '\f058';
  --icon-check-square: '\f14a';
  --icon-check-square-o: '\f046';
  --icon-circle-thin: '\f1db';
  --icon-cloud-download: '\f0ed';
  --default-autocomplete-highlight: #fafafa;
  --default-badge-bg: #444;
  --default-badge-fg: #fff;
  --default-datalist-item-highlight: rgba(0, 0, 0, 0.05);
  --default-datalist-border-color: rgba(136, 136, 136, 0.15);
  --default-overlay-fg: #222;
  --default-overlay-bg: #fff;
  --default-overlay-width: 100vw;
  --default-overlay-margin: 0 auto;
  --default-overlay-x-fg: #fff;
  --default-overlay-x-bg: #26c7d9;
  --default-panel-width: 30em;
  --default-panel-bg: #222;
  --default-panel-fg: #fff;
  --default-panel-accent: #26c7d9;
  --default-panel-x-fg: #fff;
  --default-panel-x-bg: #26c7d9;
  --default-selection-unchecked-icon: var(--icon-circle-thin);
  --default-selection-checked-icon: var(--icon-check-circle);
  --default-selection-color: #4A93B9;
  --default-switch-handle-color: #fff;
  --default-switch-color: #bfbfc1;
  --default-switch-color-active: #4A93B9;
  --default-tooltip-bg: #222;
  --default-tooltip-fg: #fff;
  --default-tooltip-shadow: #000;
}
.sim-selection {
  display: inline-block;
}
.sim-selection label {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: baseline;
        -ms-flex-align: baseline;
            align-items: baseline;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}
.sim-selection label.disabled {
      cursor: not-allowed;
      color: inherit;
}
.sim-selection input {
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    appearance: none;
    outline: none;
    font: inherit;
    cursor: inherit;
    color: inherit;
    width: 1em;
    height: 1em;
    margin: 0 1ch 0 0;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    will-change: transform;
    -webkit-transition: -webkit-transform var(--ms, var(--default-ms)) ease-out;
    transition: -webkit-transform var(--ms, var(--default-ms)) ease-out;
    transition: transform var(--ms, var(--default-ms)) ease-out;
    transition: transform var(--ms, var(--default-ms)) ease-out, -webkit-transform var(--ms, var(--default-ms)) ease-out;
}
.sim-selection input:active:not(:disabled) {
      -webkit-transform: scale(0.9);
              transform: scale(0.9);
}
.sim-selection input::before {
      content: var(--selection-unchecked-icon, var(--default-selection-unchecked-icon));
      font-family: var(--icon-font-family, var(--default-icon-font-family));
}
.sim-selection input:checked {
      color: var(--selection-color, var(--default-selection-color));
}
.sim-selection input:checked::before {
        content: var(--selection-checked-icon, var(--default-selection-checked-icon));
}
.sim-selection label.disabled {
    opacity: .5;
}
.sim-selection input::before {
    opacity: .4;
}
.sim-selection input:checked::before {
    opacity: 1;
}



