
.sim-overlay {
  opacity: 0;
  z-index: 2;
  position: fixed;
  bottom: 100%;
  height: 100vh;
  left: 0;
  right: 0;
  will-change: transform;
  -webkit-transition: opacity var(--ms, var(--default-ms)) linear, -webkit-transform var(--ms, var(--default-ms)) ease-out;
  transition: opacity var(--ms, var(--default-ms)) linear, -webkit-transform var(--ms, var(--default-ms)) ease-out;
  transition: transform var(--ms, var(--default-ms)) ease-out, opacity var(--ms, var(--default-ms)) linear;
  transition: transform var(--ms, var(--default-ms)) ease-out, opacity var(--ms, var(--default-ms)) linear, -webkit-transform var(--ms, var(--default-ms)) ease-out;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.sim-overlay.active {
    -webkit-transform: translateY(100%);
            transform: translateY(100%);
    opacity: 1;
}
.sim-overlay.active ~ .app-content {
      -webkit-transform: translateY(15vh);
              transform: translateY(15vh);
}
.sim-overlay.active .sim-overlay__dismiss {
      -webkit-transform: translate(-50%, 50%) scale(1);
              transform: translate(-50%, 50%) scale(1);
}
.sim-overlay--content {
    overflow: auto;
}
.sim-overlay__dismiss {
    line-height: 0;
    white-space: nowrap;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-transition: -webkit-transform var(--ms, var(--default-ms)) ease-out;
    transition: -webkit-transform var(--ms, var(--default-ms)) ease-out;
    transition: transform var(--ms, var(--default-ms)) ease-out;
    transition: transform var(--ms, var(--default-ms)) ease-out, -webkit-transform var(--ms, var(--default-ms)) ease-out;
    -webkit-transition-delay: calc(var(--ms, var(--default-ms)) / 2);
            transition-delay: calc(var(--ms, var(--default-ms)) / 2);
    -webkit-transform: translate(-50%, 50%) scale(0);
            transform: translate(-50%, 50%) scale(0);
}



