
.sim-panel {
  z-index: 3;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 100%;
  width: var(--panel-width, var(--default-panel-width));
  max-width: 100vw;
  will-change: transform;
  -webkit-transition: -webkit-transform var(--ms, var(--default-ms)) ease-out;
  transition: -webkit-transform var(--ms, var(--default-ms)) ease-out;
  transition: transform var(--ms, var(--default-ms)) ease-out;
  transition: transform var(--ms, var(--default-ms)) ease-out, -webkit-transform var(--ms, var(--default-ms)) ease-out;
}
.sim-panel__dismiss {
    line-height: 0;
    white-space: nowrap;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-transition: -webkit-transform var(--ms, var(--default-ms)) ease-out;
    transition: -webkit-transform var(--ms, var(--default-ms)) ease-out;
    transition: transform var(--ms, var(--default-ms)) ease-out;
    transition: transform var(--ms, var(--default-ms)) ease-out, -webkit-transform var(--ms, var(--default-ms)) ease-out;
    -webkit-transition-delay: calc(var(--ms, var(--default-ms)) / 2);
            transition-delay: calc(var(--ms, var(--default-ms)) / 2);
    -webkit-transform: translate(-50%, 50%) scale(0);
            transform: translate(-50%, 50%) scale(0);
}
.sim-panel.active {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
.sim-panel.active .sim-panel__dismiss {
      -webkit-transform: translate(-50%, 50%) scale(1);
              transform: translate(-50%, 50%) scale(1);
}



