
.sim-icontext {
  display: -webkit-inline-box;
  display: -ms-inline-flexbox;
  display: inline-flex;
}
.sim-icontext * + span {
    margin-left: .5em;
}
.sim-icontext .sim-icon {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 auto;
            flex: 0 0 auto;
}
.sim-icontext.circled .sim-icon,
  .sim-icontext.rounded .sim-icon,
  .sim-icontext.squared .sim-icon {
    width: 1.5em;
    height: 1.5em;
    margin: -0.75em 0;
    display: -webkit-inline-box;
    display: -ms-inline-flexbox;
    display: inline-flex;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-shadow: 0 0 0 .125em;
            box-shadow: 0 0 0 .125em;
}
.sim-icontext.rounded .sim-icon {
    border-radius: .3em;
}
.sim-icontext.circled .sim-icon {
    border-radius: .75em;
}



